@lost flexbox flex;
body {
  background-color: #f3f3f3 !important;
  font-size: 14px;
  &.dev {
    div,
    ul {
      box-shadow: inset 0 0 0 0.2px #f33;
    }
  }
}
.fa {
  display: inline-flex;
}
.transition {
  transition: all ease 0.3s;
}

.bg-cus-gray {
  background-color: #f3f3f3;
}

.navbar {
  &.fixed-navbar {
    position: fixed;
    width: 100%;
    z-index: 90;
  }
}
.eff-navbar {
  padding-top: 48px;
}
.cat_list {
  background: url(//pics.gmkt.kr/pc/ko/expressshop/combined-shape.png) repeat-x
    left bottom;
}
.jagg123:before {
  background: linear-gradient(
      -30deg,
      pink 0,
      pink 13px,
      #000 13px,
      #000 14px,
      transparent 14px,
      transparent 16px
    ),
    linear-gradient(
      30deg,
      pink 0,
      pink 13px,
      #000 13%,
      #000 14px,
      transparent 14px,
      transparent 16px
    );
  content: "";
  background-position: 10px 0px;
  background-repeat: repeat-x;
  background-size: 20px 19px;
  position: absolute;
  top: -8px;
  left: 0px;
  width: 100%;
  height: 8px;
}

/*  Lost, A Next Level Grid System */
ul.items_lists {
  lost-center: 100%;
  li {
    margin-bottom: 10px;

    lost-column: 1/2 2 10px;
    &.link_deal-bnr {
      a {
        background-image: url(http://image.gmarket.co.kr/hanbando/202003/473f9a7b-756b-48eb-a860-99f497392f87.jpg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        overflow: hidden;
      }
    }
  }
}
div.search-btn {
  height: 44px;
  cursor: pointer;
}
.carousel-cmm {
  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }
}
.banner_detail_price {
	top: 50px;
}
@media (min-width: 768px) {
  ul.items_lists li {
    lost-column: 1/4 4 10px;
  }
  ul.items_lists li:nth-last-child(1) {
    display: none;
  }
  .leafletPromotionPage_1 ul.items_lists li:nth-last-child(2) {
    display: none;
  }
  .fa-shopping-cart {
    font-size: 1.6em;
  }
.carousel-cmm {
  .swiper-button-next,
  .swiper-button-prev {
    display: flex;
  }
}

}
@media (min-width: 1024px) {
  ul.items_lists li {
    lost-column: 1/5 5 10px;
  }
  ul.items_lists li:nth-last-child(1) {
    display: block;
  }
  .leafletPromotionPage_1 ul.items_lists li:nth-last-child(2) {
    display: block;
  }
  .fa-shopping-cart {
    font-size: 2em;
  }
  .banner_detail_price {
	top: 60px;
}
}
@media (min-width: 1280px) {
  ul.items_lists li {
    lost-column: 1/5 5 20px;
    margin-bottom: 20px;
  }
    .banner_detail_price {
	top: 75px;
}
}

.items_lists-evt2 {
  lost-center: 100%;
  li {
    margin-bottom: 10px;
    lost-column: 1/4 4 10px;
  }
}

.carousel-cmm {
  .swiper-button-next,
  .swiper-button-prev {
    color: #595959;
  }
  .swiper-button-next {
    right: -28px;
  }
  .swiper-button-prev {
    left: -28px;
  }

  .swiper-button-next:after,
  .swiper-button-prev:after {
    font-family: sans-serif;
    content: "";
    font-size: 0;
  }
}
.carousel-btn-cont {
  top: -50px;
  right: 0;
  width: 200px;
  .swiper-container-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    width: auto;
  }
  .swiper-pagination {
    position: static;
    bottom: 0;
  }
  .swiper-button-next,
  .swiper-button-prev {
    position: static;
    display: block;
    width: auto;
    height: auto;
    margin-top: 0;
    color: #595959;
  }
  .swiper-button-next:after,
  .swiper-button-prev:after {
    font-family: sans-serif;
    content: "";
    font-size: 0;
  }
  .swiper-button-next.swiper-button-disabled,
  .swiper-button-prev.swiper-button-disabled {
    opacity: 1;
  }
}

.swiper-pagination-fraction {
	color: #909090;
	.swiper-pagination-current {
		color: #303030;
	}
}

.list__legal-info {
  li {
    font-size: 13px;
    margin-right: 20px;
    line-height: 2;
    a {
      position: relative;
      display: inline-block;
      margin-right: 10px;
      &:after {
        content: ">";
        display: inline-block;
        margin-left: 5px;
      }
    }
    > * {
      color: #999;
      font-weight: 500;
    }
  }
}

.item-count {
  position: relative;
  border: 1px solid #d2d2d2;
  padding: 13px;
  &:before {
    content: "";
    width: 13px;
    height: 1px;
    background: #555;
    position: absolute;
    left: 7px;
  }
  &:after {
    content: "";
    height: 13px;
    width: 1px;
    background: #555;
    position: absolute;
    top: 7px;
  }
}
